body{
  font-family: "Kodchasan", sans-serif; 
  width: 100%;
  margin: 0;
  padding: 0;
}
.App{
  width: 100%;
  margin: 0;
  padding: 0;
}
.container{
  padding: 0 2em 0 2em;
  width: 100%;
  margin: 0;
  padding: 0;
}
/*header component: houses 3 sections: logo, nav-bar, search*/
.header{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 2em 4em;
}
.n-logo{
  width: 120px;
}

.cart_icon, .search_icon{
  width: 23px;
  height: 23px;
  padding: 15px;
}
.cart_icon:hover, .search_icon:hover{
  background-color: #5e17eb2a;
  border-radius: 50%;
  
}

/*nav-bar (2nd section)*/
.nav-bar ul{
  width: 200px;
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.nav-bar ul li:hover{
  font-weight: 700;
  color: #5E17EB;
}
/*search (3rd section)*/
.search{
  margin: 0;
  padding: 2px 0;
  display: grid;
  grid-template-columns: 2fr 0.5fr 0.5fr;
}
/*search input box*/
.search-in{
  margin-top: 9px;
  height: 20px;
  padding: 0.3rem;
  border-radius: 8px;
  border-style: solid;
  box-shadow: none;
}



.App {
  text-align: center;
  margin: 0;
  padding: 0;
  width: 100%;
}

.home-cont{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}
.welcome, .main_img{
    width: 200px;
    height: 210px;
    text-align: left;
    padding: 1rem;
}
.welcome button, .add-to-cart{
    color:white;
    width: 200px;
    padding: 1rem;
    border:none;
    background-color: #5E17EB;
}
.welcome button:hover, .add-to-cart:hover{
    -webkit-filter: brightness(40%);
            filter: brightness(40%);
}

/*Hot Picks*/
.node-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width: 100;
    margin: 2em;
}
.hotPicks-item{
    width: 20em;
    height: 28em;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.prod-img{
    width: 200px;
    margin-top: 1rem;
}

.footer-cont{
    width: 100%;
    background-color: #5E17EB;
    color:white;
    height: 150px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.socials{
    display: flex;
    justify-content: space-between;
    width: 100px;
}
.socials a i{
    color: white;
}
.socials a i:hover{
    color: rgba(255, 255, 255, 0.576);
}
